<template>
    <div class="personal_comment_layer">
        <header class="login-header-bar clearfix">
            <a onclick="window.history.go(-1);" class="login-back-btn"></a>
            <h2 class="login-title">评论</h2>
        </header>
        <section class="zan-box">
            <ul>
                <li class="who-zan-item" v-for="(item,index) in commentsData">
                    <div class="who-zan-top">
                        <div class="who-zan-names">
                            <img :src="item.article.avatar" alt="" class="who-ct-icon">
                            <div class="who-zan-infos">
                                <p class="who-zan-name">
                                  {{item.article.author}}
                                </p>
                                <p class="who-zan-time">
                                  {{item.article.created_at | filterTime}}
                                </p>
                            </div>
                        </div>
                        <div class="zan-w clearfix">
                       <span class="zan-article">
                           {{item.content}}
                       </span>
                        </div>
                        <a @click="goResponsePage(item.article.id,item.id,item.commentator.id)" class="pinglun-huifu">
                            回复
                        </a>
                    </div>
                    <div class="who-zan-bottom">
                        <a  class="who-zan-article" @click="goSomePage($event)">
                            <img v-lazy="item.article.image" alt="" class="article-pic">
                            <div class="article-content-box">
                                <h4 class="article-title">
                                  {{item.article.title}}
                                </h4>
                                <p class="article-content">
                                  {{item.article.body}}
                                </p>
                            </div>
                        </a>
                    </div>
                </li>
            </ul>
        </section>
    </div>
</template>
<script>
  import Api from '../../api/api';
  import {getCommentInfo} from '../../api/comment';
    export default{
        data(){
          return{
              commentsData:[],
          }
        },
        mounted(){
          this.$nextTick(function () {
            this.getUserCommentInfos();
          });
        },
      filters:{
        filterTime(value){
          return value.split("T")[0];
        }
      },
        methods:{
          getUserCommentInfos(){
            const _this = this;
            getCommentInfo(Api.USERCOMMENTS).then((res)=>{
//                console.log("获取到的评论信息是:",res);
               _this.initUserComment(res);
            },(err)=>{
                _this.dealErr();
            });
          },
          dealErr(){
            this.$store.dispatch('hideLoading');
            this.showMsg('获取评论信息失败，请重试!');
          },
          showMsg(str){
            this.$toast({
              message: str,
              duration: 2000,
            });
          },
          initUserComment(res){
            if(res.status === 200 && res.data.data!==null){
                this.commentsData = res.data.data.detail;
            }else{
                this.dealErr();
            }
          },
            /**
             * switch the response page(回复界面)
             */
            goResponsePage(articldId,commentId,commenter_id){
              this.$router.push({path: `/replymsg/${articldId}/${commentId}/${commenter_id}`});
            },
            goSomePage($event){
                this.$router.push({path: '/newsdetail', replace: true});
                $event.preventDefault();
            }
        }
    }
</script>
<style scoped>
    @import "../../assets/css/pcenpinglun.css";
    body {
        background-color: #EDEEF0;
    }
</style>



